import React from 'react';

import logo from './img/logo.png';
import twitter from './img/twitter.png';
import discord from './img/discord.png';

function Footer() {
    return (
        <React.Fragment>
            <div id="footer-logo">
                <img src={logo} alt="" className="pixelated" />
            </div>
            <div id="footer-icons">
                <a href="https://discord.gg/5ttWm9FpCm" target="_blank" rel="noreferrer" className="discord"><img src={discord} alt="" className="pixelated" /></a>
                <a href="https://twitter.com/MetaStonez" target="_blank" rel="noreferrer" className="twitter"><img src={twitter} alt="" className="pixelated" /></a>
            </div>
        </React.Fragment>
    );
}

export default Footer;
