import React from 'react';
import { useWeb3React } from "@web3-react/core";

import { injected } from "./Connector";

function Wallet() {
    const { active, activate } = useWeb3React();

    async function connect() {
        try {
            await activate(injected);
        } catch (ex) {
            console.log(ex)
        }
    }

    return (
        <React.Fragment>
            {active ? <button className="black-text">WALLET CONNECTED</button> : <button onClick={connect} className="black-text">CONNECT WALLET</button> }
        </React.Fragment>
    );
}

export default Wallet;
