import React from 'react';

import partnerKaijuKingz from './img/partner-kaijukingz.png';
import partnerGalacticApes from './img/partner-galacticapes.png';
import partnerNanopass from './img/partner-nanopass.png';
import partnerRengoku from './img/partner-rengoku.png';
import partnerSvs from './img/partner-svs.png';
import partnerBearx from './img/partner-bearx.png';

function Mint() {
    return (
        <React.Fragment>
            <h1>OFFICIAL COLLABORATIONS</h1>
            <p>Every community collaboration that MetaStonez has utilizes unique interoperability within the space. These partnerships include, but are not limited to, direct integration into their respective metaverses, features in upcoming drops and collections, crossover collections that are unique to MetaStonez and partner holders, and exclusive access to future mints, merchandise and other integration benefits.</p>
            <div className="partner-container">
                <div><img src={partnerKaijuKingz} alt="" /></div>
                <div><img src={partnerGalacticApes} alt="" /></div>
                <div><img src={partnerNanopass} alt="" /></div>
                <div><img src={partnerRengoku} alt="" /></div>
                <div><img src={partnerSvs} alt="" /></div>
                <div><img src={partnerBearx} alt="" /></div>
            </div>
        </React.Fragment>
    );
}

export default Mint;
