import React from 'react';

// import city from './video/city.mp4';

import archetype01 from './img/archetype-01.png';
import archetype02 from './img/archetype-02.png';
import archetype03 from './img/archetype-03.png';
import archetypeStone01 from './img/archetype-01-stone.png';
import archetypeStone02 from './img/archetype-02-stone.png';
import archetypeStone03 from './img/archetype-03-stone.png';

function About() {
    return (
        <React.Fragment>
            <div id="city-container">
                <div id="video-overlay">&nbsp;</div>
                <video id="video-city" preload="true" autoPlay playsInline loop muted>
                    {/* <source src={city} type="video/mp4" />  */}
                </video>
            </div>
            <h1>RISE OF THE STONEVERSE</h1>
            <div id="section-about-content">
                <p>MetaStonez is a generated Non-Fungible Token project on the Ethereum blockchain. MetaStonez exist in 100x100 pixel containers depicting Stone Giants born from the Natural Elements, including Igneous Rocks as well as Precious Metals. The traits and varieties of MetaStonez are tremendous as there are only 10101 MetaStonez in existence. Of these, there are 1321 Genesis Stonez and 8760 Origin Stonez.</p>
                <div id="archetypes-container">
                        <div className="archetype archetype-01">
                            <img src={archetype01} className="pixelated" alt="" />
                            <div>
                                <p className="archetype-type">[ TYPE-01 ]</p>
                                <h3 className="yellow-text">NATURAL STONEZ</h3>
                                <p className="archetype-subtitle">CLASTIC SEDIMENT</p>
                                <div className="archetype-content">
                                    <div>
                                        <p className="yellow-text archetype-subtitle">Grown on the exterior<br />of Earth's crust<br />from extreme pressure</p>
                                    </div>
                                    <img src={archetypeStone01} className="pixelated" alt="" />
                                </div>
                            </div>
                        </div>
                        <div className="archetype archetype-02">
                            <img src={archetype02} className="pixelated" alt="" />
                            <div>
                                <p className="archetype-type">[ TYPE-02 ]</p>
                                <h3 className="yellow-text">INTERNAL STONEZ</h3>
                                <p className="archetype-subtitle">EXTRUSIVE IGNEOUS</p>
                                <div className="archetype-content">
                                    <div>
                                        <p className="yellow-text archetype-subtitle">Grown within<br />the Earth's crust<br />from extreme heat</p>
                                    </div>
                                    <img src={archetypeStone02} className="pixelated" alt="" />
                                </div>
                            </div>
                        </div>
                        <div className="archetype archetype-03">
                            <img src={archetype03} className="pixelated" alt="" />
                            <div>
                                <p className="archetype-type">[ TYPE-03 ]</p>
                                <h3 className="yellow-text">PRECIOUS STONEZ</h3>
                                <p className="archetype-subtitle">CRYSTALLINE SEDIMENT</p>
                                <div className="archetype-content">
                                    <div>
                                        <p className="yellow-text archetype-subtitle">Grown deep within the<br />Earth's crust from<br />extreme heat + pressure</p>
                                    </div>
                                    <img src={archetypeStone03} className="pixelated" alt="" />
                                </div>
                            </div>
                        </div>
                    </div>

                {/* <div id="about-text-container">
                    <div id="about-text">
                        <h2>RISE OF THE<br />METASTONEZ</h2>
                        <p>In the distant future, a plague hit human society and the remnants of life started to grow from natural stone. Over time, these ethereal creatures made earth their stomping grounds. The MetaStonez are divided into 4 factions all representing their respective tribes (Natural Stones, Infernal Stones, Precious Gemstones, and Elementals). Each faction has its unique identity and powers. Which MetaStonez will be your calling?</p>
                    </div>
                </div>
                <div id="tesseract">
                    <img src={tesseract} className="pixelated" alt="" />
                </div> */}
            </div>
        </React.Fragment>
    );
}

export default About;