import React, { Component } from 'react';

import Header from "./Header"
import Mint from "./Mint"
import About from "./About"
import Roadmap from "./Roadmap"
import Partners from "./Partners"
import Team from "./Team"
import Questions from "./Questions"
import Footer from "./Footer"

import './App.css';
import './App-phone.css';

class App extends Component {
    constructor() {
        super();
        this.handleResize = this.handleResize.bind(this);
    }

    componentDidMount() {
        window.addEventListener("resize", this.handleResize);
        window.dispatchEvent(new Event('resize'));
    }

    componentWillUnmount() {
        window.addEventListener("resize", null);
    }

    handleResize() {
        // Menu toggler enabling menu always when resizing to large window
        if (window.innerWidth > 960) {
            document.getElementById("menu-container").style.display = "flex";
        }
        else {
            document.getElementById("menu-container").style.display = "none";
        }

        // Roadmap dotted lines
        let prevItem = {};
        let currItem = {};
        let latestItem = {};
        let items = document.getElementsByClassName("roadmap-future-item-container");
        for (let i = 0; i < items.length; i++) {
            currItem = items[i].getBoundingClientRect();
            if (prevItem && prevItem.top < currItem.top) {
                items[0].classList.add("dotted-border-left-and-top");
                items[i].classList.remove("dotted-border-top");
                items[i].classList.add("dotted-border-left-and-top");
                latestItem = items[i];
            }
            else {
                items[i].classList.add("dotted-border-top");
                items[i].classList.remove("dotted-border-left-and-top");
            }
            prevItem = currItem;
        }
        if (latestItem.classList) {
            latestItem.classList.remove("dotted-border-left-and-top");
            latestItem.classList.add("dotted-border-top");
        }

        // Handle team background switch
        if (document.getElementById("caves-container").clientHeight > 900) {
            document.getElementById("video-caves01").classList = "video-caves-container video-caves-filter-top";
            document.getElementById("video-caves02").style.display = "unset";
        }
        else {
            document.getElementById("video-caves01").classList = "video-caves-container";
            document.getElementById("video-caves02").style.display = "none";
        }
    }

    render() {
        return (
            <div id="app" className="white-text">
                <div id="section-header">
                    <Header />
                </div>

                <div id="section-mint">
                    <Mint />
                </div>

                <div id="section-about">
                    <About />
                </div>

                {/* <div id="section-archetypes">
                    <h1>STONEZ ARCHETYPE</h1>
                    <div id="archetypes-container">
                        <div className="archetype archetype-01">
                            <img src={archetype01} className="pixelated" alt="" />
                            <div>
                                <p>[ TYPE-01 ]</p>
                                <h3 className="yellow-text">NATURAL STONEZ</h3>
                                <p className="archetype-subtitle">CLASTIC SEDIMENT</p>
                                <div className="archetype-content">
                                    <div>
                                        <p className="yellow-text archetype-subtitle">Grown on the exterior of Earth's crust from extreme pressure</p>
                                    </div>
                                    <img src={archetypeStone01} className="pixelated" alt="" />
                                </div>
                            </div>
                        </div>
                        <div className="archetype archetype-02">
                            <img src={archetype02} className="pixelated" alt="" />
                            <div>
                                <p>[ TYPE-02 ]</p>
                                <h3 className="yellow-text">INTERNAL STONEZ</h3>
                                <p className="archetype-subtitle">EXTRUSIVE IGNEOUS</p>
                                <div className="archetype-content">
                                    <div>
                                        <p className="yellow-text archetype-subtitle">Grown within the Earth's crust from extreme heat</p>
                                    </div>
                                    <img src={archetypeStone02} className="pixelated" alt="" />
                                </div>
                            </div>
                        </div>
                        <div className="archetype archetype-03">
                            <img src={archetype03} className="pixelated" alt="" />
                            <div>
                                <p>[ TYPE-03 ]</p>
                                <h3 className="yellow-text">PRECIOUS STONEZ</h3>
                                <p className="archetype-subtitle">CRYSTALLINE SEDIMENT</p>
                                <div className="archetype-content">
                                    <div>
                                        <p className="yellow-text archetype-subtitle">Grown deep within the Earth's crust from extreme heat + pressure</p>
                                    </div>
                                    <img src={archetypeStone03} className="pixelated" alt="" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div> */}

                <div id="section-roadmap">
                    <Roadmap />
                </div>

                <div id="section-partners">
                    <Partners />
                </div>

                <div id="section-team">
                    <Team />
                </div>

                <div id="section-questions">
                    <Questions />
                </div>

                <div id="section-footer">
                    <Footer />
                </div>

            </div>
        );
    } 
}

export default App;
