import React from 'react';
import $ from 'jquery';
import { animateScroll as scroll } from 'react-scroll';

import Wallet from "./Wallet"

import volcano from './video/volcano.mp4';
import twitter from './img/twitter.png';
import discord from './img/discord.png';
import burger from './img/burger.png';
import cubes from './img/cubes.gif';
import logo from './img/logo.png';

function Header() {
    function toggleMenu() {
        $("#menu-container").slideToggle(function() {
            if ($(this).is(':visible')) {
                $(this).css("display", "flex");
            }
        });
    }

    function handleMenuClick(name) {
        let elementPosition = $(name).offset().top;
        scroll.scrollTo(elementPosition, {duration: 500, delay: 0, smooth: "easeInOutQuart"});

        // .offsetParent is null if element is not visible
        if (document.getElementById("menu-toggler").offsetParent) {
            toggleMenu();
        }
    }

    function handleMintClick() {
        // if mint square is larger than screen, go to top, otherwise go to center
        let scrollPosition;
        if ($("#mint-square").innerHeight() > window.innerHeight) {
            console.log("top");
            scrollPosition = $("#section-mint").offset().top;
        }
        else {
            console.log("center");
            let topPosition = $("#section-mint").offset().top;
            let bottomPosition = topPosition + $("#section-mint").outerHeight();
            scrollPosition = (topPosition + bottomPosition - window.innerHeight) / 2;
        }

        scroll.scrollTo(scrollPosition, {duration: 500, delay: 0, smooth: "easeInOutQuart"});

        // .offsetParent is null if element is not visible
        if (document.getElementById("menu-toggler").offsetParent) {
            toggleMenu();
        }
    }

    return (
        <React.Fragment>
            <video id="video-volcano" preload="true" autoPlay playsInline loop muted>
                <source src={volcano} type="video/mp4" /> 
            </video>
            <div id="top-bar">
                <div id="icons">
                    <a href="https://discord.gg/5ttWm9FpCm" target="_blank" rel="noreferrer" className="discord"><img src={discord} alt="" /></a>
                    <a href="https://twitter.com/MetaStonez" target="_blank" rel="noreferrer" className="twitter"><img src={twitter} alt="" /></a>
                </div>
                <div id="links">
                    <div id="menu-toggler" onClick={toggleMenu}><img src={burger} alt="" /></div>
                    <ul id="menu-container">
                        <li onClick={() => {handleMintClick()}}>MINT</li>
                        <li onClick={() => {handleMenuClick("#section-about")}}>ABOUT</li>
                        <li onClick={() => {handleMenuClick("#section-team")}}>TEAM</li>
                    </ul>
                </div>
                <div id="wallet">
                    <Wallet />
                </div>
            </div>

            <div id="wallet-phone">
                <Wallet />
            </div>

            <div id="cubes">
                <img src={cubes} alt="" className="pixelated" />
            </div>
            <div id="logo">
                <img src={logo} className="pixelated" alt="" />
            </div>
            <div id="logo-subtext">
                <p>The MetaStonez are pixel based creatures that have risen from the earth. We exist to expand the Stoneverse by allowing NFT collectors, artists and developers an opportunity to be a part of this journey.</p>
                <p className="down-arrows">&darr; &darr; &darr;</p>
            </div>
        </React.Fragment>
    );
}

export default Header;
