import React from 'react';
import $ from 'jquery';
import { scroller } from 'react-scroll';

function Mint() {
    var lockAnimations = false;

    function closeAllQuestions(except, callback) {
        let questions = ["1", "2", "3", "4", "5", "6", "7"];
        let filteredQuestions = [];
        
        questions.forEach(questionNumber => {
            if (questionNumber !== except && $("#question-text-" + questionNumber).is(':visible')) {
                filteredQuestions.push(questionNumber);
            }
        });
        
        if (filteredQuestions.length === 0) {
            callback();
            return;
        }

        filteredQuestions.forEach((questionNumber, index, array) => {
            if (index === array.length - 1) {
                $("#question-text-" + questionNumber).slideToggle(200, callback);
                $("#question-arrows-" + questionNumber).html("&darr;&darr;&darr;");
            }
            else {
                $("#question-text-" + questionNumber).slideToggle(200);
                $("#question-arrows-" + questionNumber).html("&darr;&darr;&darr;");
            }
        });
    }

    function toggleQuestion(questionNumber) {
        if (lockAnimations) {
            return;
        }

        lockAnimations = true;
        closeAllQuestions(questionNumber, function () {
            if (!$("#question-text-" + questionNumber).is(':visible')) {
                scroller.scrollTo("question-header-" + questionNumber, {duration: 200, delay: 0, smooth: "easeInOutQuart"});
                $("#question-arrows-" + questionNumber).html("&uarr;&uarr;&uarr;");
            }
            else {
                $("#question-arrows-" + questionNumber).html("&darr;&darr;&darr;");
            }
    
            $("#question-text-" + questionNumber).slideToggle(200, function() {
                lockAnimations = false;
            });
        });
    }

    return (
        <React.Fragment>
            <div id="faq-header">
                <h1>FAQ</h1>
            </div>
            <div id="faq-container">
                <div>
                    <div onClick={() => {toggleQuestion("1")}} id="question-header-1" className="question-header yellow-text">
                        <div>What is MetaStonez? </div>
                        <div id="question-arrows-1">&darr;&darr;&darr;</div>
                    </div>
                    <p id="question-text-1" className="question-text">
                        MetaStonez is a generated Non-Fungible Token project on the Ethereum blockchain. MetaStonez exist in 100x100 pixel containers depicting Stone Giants born from the Natural Elements, including Igneous Rocks as well as Precious Metals. The traits and varieties of MetaStonez are tremendous as there are only 10101 MetaStonez in existence. Of these, there are 1321 Genesis Stonez and 8780 Origin Stonez.<br />
                        <br />
                        We're relying on the strength of our team, our DAO, our forward facing goals, and interoperability with our partners to make the best decisions going forward in the development of the Metastonez while providing a minting process that is sensitive to gas waste so that the DAO is properly funded to seek through our goals and the team is able to commit their time, efforts, and resources to scaling Metastonez to the metaverse.
                    </p>
                </div>
                <div>
                    <div onClick={() => {toggleQuestion("2")}} id="question-header-2" className="question-header yellow-text">
                        <div>How many types of MetaStonez are there?</div>
                        <div id="question-arrows-2">&darr;&darr;&darr;</div>
                    </div>
                    <p id="question-text-2" className="question-text">
                        <b>Genesis Metastonez</b> - 1321 available - 3 MetaStone Archetypes + Detailed Backgrounds<br /><br />
                        <b>Origin Metastonez</b> - 8760 available - 3 MetaStone Archetypes + Solid Color Backgrounds<br /><br />
                        <b>Elemental Metastonez</b> - 1-of-1 Metastonez  + Animation
                    </p>
                </div>
                <div>
                    <div onClick={() => {toggleQuestion("3")}} id="question-header-3" className="question-header yellow-text">
                        <div>What is the function/utility of MetaStonez DAO? </div>
                        <div id="question-arrows-3">&darr;&darr;&darr;</div>
                    </div>
                    <p id="question-text-3" className="question-text">
                        The function of the MetaStonez DAO is to have MetaStonez holders vote on proposals or delegate their votes to a third party.<br />
                        <br />
                        A minimum of one MetaStonez is required to submit a proposal.<br />
                        <br />
                        MetaStonez brings an ace team and a community DAO structure, looking to build a well-funded and powerful long term Metaverse brand to build a future that both the team and DAO envision together.<br />
                        <br />
                        The MetaStonez DAO treasury will receive 10% of ETH proceeds from all Genesis and Origin mint sales as well as 50% of Elemental/1:1 auctions.<br />
                        On all secondary market sales, the treasury will receive 20% of all royalties.<br />
                        <br />
                        MetaStonez DAO, which is based on Compound Governance, is also the main governing body of the Metastonez ecosystem. Each MetaStone is an irrevocable member of Metastonez DAO and entitled to one vote in all governance matters.
                    </p>
                </div>
                <div>
                    <div onClick={() => {toggleQuestion("4")}} id="question-header-4" className="question-header yellow-text">
                        <div>How will the MetaStonez Mint/Auction occur? </div>
                        <div id="question-arrows-4">&darr;&darr;&darr;</div>
                    </div>
                    <p id="question-text-4" className="question-text">
                        The MetaStonez mint will be comprised of the Genesis &amp; Origin fixed price sale as well as the Elemental Metastonez Auction, the first one being a Genesis only sale and the second one (arriving shortly after) being an Origins and Genesis sale. The means by which one can mint a MetaStonez will be that there will be a presale period wherein only Whitelisted wallets are able to mint the MetaStonez. The collection for these wallets will occur 24-72 hours prior to each respective mint, means by which of procuring a Whitelist are enumerated on Discord and Twitter.<br />
                        <br />
                        The Elemental MetaStonez auction will be comprised of the coveted 1/1 Elementals which 50% of the proceeds go directly to the DAO treasury.
                    </p>
                </div>
                <div>
                    <div onClick={() => {toggleQuestion("5")}} id="question-header-5" className="question-header yellow-text">
                        <div>What is the MetaStonez Value Cycle? </div>
                        <div id="question-arrows-5">&darr;&darr;&darr;</div>
                    </div>
                    <p id="question-text-5" className="question-text">
                        MetaStonez without the DAO or Treasury, what would it be....? Just another piece of graphic art amongst a forever growing market. We reach peaks of value when we marry these facets together. We build the culture with the characters and tempo which benefit the DAO &amp; treasury.<br />
                        <br />
                        To further cement the value cycle, we must then integrate the community into the DAO. It is here, where Metastonez separate itself from just another project to an ecosystem that can span the world and have meaningful impact in DAO governance, Web3 and cryptocurrency.<br />
                        <br />
                        The question remains; how do we use it? How do we leverage the treasury to garner awareness while simultaneously spreading the culture to have a net positive to both the DAO &amp; treasury?<br />
                        <br />
                        Much like Ethereum, Metastonez is an open-source project. Both the code &amp; art are open-source and housed in a public domain so that anyone can either build on the art or on the codebase.<br />
                        <br />
                        Our mission is to be bold &amp; imaginative in how we operate the Metastonez DAO &amp; treasury. We should seek projects that cultivate the community. We aim to take calculated risks to fund high quality proposals that can both benefit the culture and software.<br />
                        <br />
                        We sit at the precipice of a new beginning, a new approach to governance and economics. It is our time to take a stance and bring forward the people and communities that can take expand the MetaStonez foundation to be consumed by the world.
                    </p>
                </div>
                <div>
                    <div onClick={() => {toggleQuestion("6")}} id="question-header-6" className="question-header yellow-text">
                        <div>What are the Benefits/Utility of MetaStonez? </div>
                        <div id="question-arrows-6">&darr;&darr;&darr;</div>
                    </div>
                    <p id="question-text-6" className="question-text">
                        In addition to the public roadmap, the team is working on value creation for membership and participation which is still being refined and scoped. We want to make sure everything is done the right way and commitments are firm when we publicize them, so we can prove sustainable long term value to all holders through all of our future plans.<br />
                        <br />
                        Rest assured, bringing value and quality back to our holders is something that we have committed ourselves fully to doing. If and when any such commitments have been solidified, we will maintain the utmost transparency and fill our community in of all the details in a timely manner.
                    </p>
                </div>
                <div>
                    <div onClick={() => {toggleQuestion("7")}} id="question-header-7" className="question-header yellow-text">
                        <div>What value does MetaStonez bring with Official Partners? </div>
                        <div id="question-arrows-7">&darr;&darr;&darr;</div>
                    </div>
                    <p id="question-text-7" className="question-text">
                        Every partnership that MetaStonez has utilizes unique interoperability within the space.<br />
                        <br />
                        These partnerships include, but are not limited to, direct integration into their respective Metaverse, features in upcoming drops and collections, crossover collections that are unique to MetaStonez and partner holders, and exclusive access to future mints, merchandise and other integration benefits.<br />
                    </p>
                </div>
            </div>            
        </React.Fragment>
    );
}

export default Mint;
